*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.card{
    background-color: lightseagreen;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 10px 15px;
    padding: 10px;
    border-radius: 5px;
    transition: all 0.1s ease-in-out;
    color:white;
    cursor: pointer;
    box-shadow: lightcoral 1px 1px 30px 5px;
}
.card h4{
    font-size:1.2rem
}
.card:hover{
    transform: scale(1.03);
}
.card img{
    width: 18vw;
}